@charset "utf-8";
/* Global Styles */

.body {
  margin-left: auto;
  margin-right: auto;
  width: 92%;
  max-width: 960px;
  padding-left: 2.275%;
  padding-right: 2.275%;
  position: relative;
  top:55px;
}
.mainContent .section1 .sectionTitle {
  float: bottom;
}

/* Header */
.header {
  text-align: center;
  display: block;
}
header .logoPlaceholder span {
  width: 80px;
  height: 22px;
  font-family: 'Montserrat', sans-serif;
  color: rgba(255,255,255,1.00);
  font-size: 30px;
  font-weight: 700;
  line-height: 53px;
}
.header .profilePhoto {
  opacity: 99%;
  max-width: 259px;
  border-radius: 50%;
  height: 259px;
  float: left;
  margin: 0 auto;
}
.header .profileHeader .h1 {
  font-family: 'Montserrat', sans-serif;
  color: rgba(31, 24, 24, 0.91);
  font-size: 30px;
  font-weight: 700;
  line-height: 24px;
}
.header .profileHeader .h3 {
  font-family: sans-serif;
  color: rgba(77,68,68,1.00);
  font-size: 16px;
  font-weight: 700;
  line-height: 24px;
}
hr .hr{
  background-color: rgba(208,207,207,1.00);
  height: 1px;
}
.header .profileHeader p {
  font-family: sans-serif;
  color: rgba(146,146,146,1.00);
  font-size: 14px;
  font-weight: 400;
  line-height: 1.8;
  overflow-y: auto;
}
.header .socialNetworkNavBar {
  text-align: center;
  display: block;
  margin-top: 60px;
  clear: both;
}
.header .socialNetworkNavBar .socialNetworkNav {
  border-radius: 50%;
  cursor: pointer;
}
.header .socialNetworkNavBar .socialNetworkNav img:hover {
  opacity: 0.5;
}
/* Main Content sections */
.mainContent .section1 {
  display: block;
}
.section1 .sectionTitle, .section2 .sectionTitle {
  padding-top: 2.5%;
  font-family: 'Montserrat', sans-serif;
  color: rgba(77,68,68,1.00);
  font-size: 18px;
  font-weight: 700;
  text-transform: uppercase;
}
.section1 .sectionTitleRule, .section2 .sectionTitleRule {
  margin: 0 3% 0 0;
  float: left;
  clear: both;
}
.section1 .sectionTitleRule2, .section2 .sectionTitleRule2 {
  background-color: rgba(208,207,207,1.00);
  width: 100%;
  height: 1px;
  padding: 0px;
}
.mainContent .section1 .section1Content {
  text-align: center;
  padding-top: 3%;
  padding-bottom: 0%;
  font-family: ProximaNova;
  font-size: 14px;
  font-weight: 100;
  color: rgba(77,68,68,1.00);
}
.mainContent .section1 .section1Content span {
  color: rgba(146, 146, 146, 1.00);
  font-family: sans-serif;
}

.section2 .sectionContentTitle {
  font-family: sans-serif;
  color: rgba(146,146,146,1.00);
  font-size: 18px;
  font-weight: 700;
}
.section2 .sectionContentSubTitle {
  font-family: sans-serif;
  color: rgba(68,67,67,1.00);
  font-size: 14px;
  font-weight: 400;
}
.section2 .sectionContent {
  font-family: sans-serif;
  color: rgba(146,146,146,1.00);
  font-size: 14px;
  font-weight: 400;
  line-height: 1.8;
}
.mainContent {
  display: block;
}
.mainContent .externalResourcesNav .externalResources {
  background-color: rgba(208,207,207,1.00);
  display: inline-block;
  text-align: center;
  margin-top: 0px;
  padding-top: 3%;
  cursor: pointer;
}
.mainContent .externalResourcesNav .externalResources:hover {
  background-color: rgba(153,153,153,1.00);
}
.externalResourcesNav .externalResources a {
  font-family: 'Montserrat', sans-serif;
  color: rgba(255,255,255,1.00);
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  text-decoration: none;
}
.stretch {
  display: inline-block;
  align-content: center;
  margin-left: 2%;
  margin-right: 2%;
}
/* Footer */
.footer .footerDisclaimer {
  font-family: sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: rgba(104,103,103,1.00);
  font-weight: 700;
}
.footer .footerDisclaimer .span {
  color: rgba(181,178,178,1.00);
  font-weight: 400;
}
.footer .footerNote {
  font-family: sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: rgba(104,103,103,1.00);
  font-weight: 700;
}
.footer .footerNote span {
  color: rgba(181,178,178,1.00);
  font-weight: 400;
}

/* Media query for Mobile devices*/
@media only screen and (min-width : 285px) and (max-width : 480px) {
  /* Header */
  .header {
    width: 100%;
    float: none;
  }
  .header .profileLogo .logoPlaceholder {
    display: inline-block;
  }
  .header .profilePhoto {
    display: inline-block;
  }
  header .profileHeader {
    text-align: center;
    margin-top: 10%;
  }
  .header .profileHeader .p {
    text-align: justify;
  }
  .header .socialNetworkNavBar {
    text-align: center;
    display: block;
    margin-top: 60px;
    clear: both;
    margin-bottom: 15%;
  }
 .header .socialNetworkNavBar .socialNetworkNav {
    width: 60px;
    height: 60px;
    display: inline-block;
    margin-right: 23px;
  }
  /* Main content sections */
  .mainContent .section1 {
    text-align: center;
    margin-bottom: 10%;
  }
  .section1 .section1Title, .section2 .section1Title {
    text-align: center;
  }
  .section1 .sectionTitleRule, .section2 .sectionTitleRule {
    text-align: center;
    width: 100%;

  }
  .section1 .sectionTitleRule2, .section2 .sectionTitleRule2 {
    display: none;
  }
  .mainContent .section1 .section1Content {
    display: inline-block;
    text-align: left;
    font-family: sans-serif;
    text-align: justify;
  }
  .mainContent .section2 {
    display: block;
    text-align: center;
  }
  .section2 .section2Content {
    margin-top: 10%;
    text-align: justify;
    margin-bottom: 10%;
  }
  .mainContent .externalResourcesNav {
    margin-bottom: 10%;
  }
  .mainContent .externalResourcesNav .externalResources {
    width: 100%;
    height: 35px;
  }
  /* Footer */
  .footer {
    display: block;
    text-align: center;
    font-family: sans-serif;
  }
}

/* Media Query for Tablets */
@media only screen and (min-width : 481px) and (max-width : 1024px) {
  /* Header */
  .header {
    width: 100%;
    float: none;
  }
  .header .profileLogo .logoPlaceholder {
    display: inline-block;
    font-family: 'Montserrat', sans-serif;
  }
  .header .profilePhoto {
    float: left;
    clear: both;
    margin-right: 3%;
    margin-top: 5%;
  }
  .header .profileHeader {
    text-align: left;
    margin-top: 7%;
  }
  .header .socialNetworkNavBar .socialNetworkNav {
    width: 74px;
    height: 74px;
    display: inline-block;
    margin-right: 23px;
  }
  /* Main content and sections */
  .section1 .section1Title, .section2 .section1Title {
    text-align: center;
  }
  .section1 .sectionTitleRule, .section2 .sectionTitleRule {
    width: 100%;
  }
  .section1 .sectionTitleRule2, .section2 .sectionTitleRule2 {
    display: none;
  }
  .mainContent .section1 .section1Content {
    display: block;
    margin: 0% 0% 0% 20%;
    font-family: sans-serif;
    margin-top: 5%;
    margin-bottom: 5%;
  }
  .mainContent .section2 {
    clear: both;
  }
  .section2 .section2Content {
    padding: 0px;
    margin-left: 20%;
    font-family: sans-serif;
    margin-top: 5%;
    margin-bottom: 5%;
  }
  .mainContent .externalResourcesNav {
    margin-left: 20%;
    margin-top: 40px;
    margin-bottom: 40px;
    text-align: center;
    padding-top: 0px;
    clear: both;
  }
  .mainContent .externalResourcesNav .externalResources {
    width: 199px;
    height: 48px;
    margin-top: 5%;
  }
  /* Footer */
  .footer .footerDisclaimer {
    float: left;
    margin-left: 3%;
    font-family: sans-serif;
  }
  .footer .footerNote {
    float: right;
    margin-right: 3%;
    font-family: sans-serif;
  }
}

/* Desktops and laptops  */
@media only screen and (min-width:1025px) {
  /* Header */
  .header .profileLogo .logoPlaceholder {
    float: left;
    margin-bottom: 50px;
  }
  .header .profilePhoto {
    float: left;
    clear: both;
    margin-right: 3%;
  }
  .header .profileHeader {
    text-align: left;
    padding-top: 10%;
  }
  .header .socialNetworkNavBar .socialNetworkNav {
    width: 74px;
    height: 74px;
    display: inline-block;
    margin-right: 23px;
  }
  /* Main content sections */
  .section1 .sectionTitleRule, .section2 .sectionTitleRule {
    width: 22.5%;
  }
  .mainContent .section1 .section1Content {
    display: block;
    margin-top: 0%;
    margin-right: 0%;
    margin-left: 10%;
    margin-bottom: 0%;
    font-family: sans-serif;
  }
  .mainContent .section2 {
    clear: both;
  }
  .section2 .section2Content {
    padding: 0px;
    margin-left: 26%;
    font-family: sans-serif;
  }
  .mainContent .externalResourcesNav {
    margin-top: 40px;
    margin-bottom: 40px;
    text-align: center;
    padding-top: 0px;
    clear: both;
  }
  .mainContent .externalResourcesNav .externalResources {
    width: 199px;
    height: 48px;
  }

  /* Footer */
  .footer .footerDisclaimer {
    float: left;
    margin-left: 3%;
    font-family: sans-serif;
  }
  .footer .footerNote {
    float: right;
    margin-right: 3%;
    font-family: Segoe, "Segoe UI", "DejaVu Sans", "Trebuchet MS", Verdana, "sans-serif"
  }

  .experienceHeaders {
    font-family: sans-serif;
    color: rgba(77,68,68,1.00);
    font-size: 18px;
    font-weight: 700;
  }

}
.languages {
  color: rgba(77,68,68,1.00);

}
.unorderedLists{
  color: rgba(146,146,146,1.00);
  font-size: 14px;
  font-weight: 400;
  line-height: 1.8;
}

.contact{
  color: rgba(77,68,68,1.00);
  float: inside;
}

.floatRight{
  float: right;
  margin-left: auto;
  margin-right: auto;
  width: 48%;
}

.floatLeft{
  float: left;
  margin-right: auto;
  margin-left: auto;
  width: 48%;
}
.floatBottom{
  float: bottom;
}
.header{
  background-color: white;
}

